.topbar {
    width: 100%;
    height: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 4%;
    box-sizing: border-box;
    flex-wrap: wrap;
}

.topbarleftalign {
    display: flex;
    align-items: center;
    height: 100%;
}

.logo {
    background-image: url(./media/logo.svg);
    height: 100px;
    width: 80px;
    margin-right: 50px;
    background-size: cover;
}

.logostaking {
    font-weight: 800;
    font-size: 30px;
    user-select: none;
}

.connectwalletbutton {
    height: 50px;
    background-color: #113CFC;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    cursor: pointer;
    padding: 0 25px;
    font-weight: 700;
    border-radius: 12px;
    position: absolute;
    top: 0px;
    right: 20px;
}

.connectwalletbuttonactive {
    background-color: #7DEBA2;
    color: white;
}

.contentcontainer {
    display: flex;
    align-items: center;
    flex-direction: column;
    position: absolute;
    top: 25px;
    width: 100%;
}

.infotext {
    margin-top: 4%;
    font-weight: 800;
    text-align: center;
    font-size: 20px;
}

.buyinputcontainer {
    width: 500px;
    background: #FFFFFF;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 14px;
    margin-top: 100px;
    display: flex;
    align-items: center;
    padding: 30px 0;
    flex-direction: column;
}

.buyinputelementcontainer{
    width: 90%;
    position: relative;
    margin: 15px 0;
}

.buyinputelement{
    width: 100%;
    height: 60px;
    background-color: #C0C0C0;
    border: none;
    border-radius: 10px;
    font-weight: 700;
    font-size: 30px;
    padding-left: 10px;
    box-sizing: border-box;
}

.buyinputlabel{
    position: absolute;
    top: 0px;
    right: 15px;
    font-weight: 700;
    font-size: 20px;
}

.purpleflokilabel{
    top: -12px;
    text-align: right;
}

.buybutton{
    margin: 15px 0 5px 0;
    border-radius: 10px;
    background-color: #87F38B;
    padding: 15px 70px;
    font-weight: 700;
    font-size: 20px;
    cursor: pointer;
}

@media only screen and (max-width: 500px) {
    .buyinputcontainer {
        width: 95%;
    }
}

@media only screen and (max-width: 581px) {
    .logostaking {
        font-size: 18px;
    }
}

@media only screen and (max-width: 900px) {
    .connectwalletbutton {
        margin-top: 100px;
    }

    .topbar {
        margin-bottom: 100px;
    }
}